.table {
  grid-area: tablePlayers;
  background: var(--color-white);
  border: 2px solid var(--color-white);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-self: center;
  gap: 2px;
  width: 12em;
  overflow: hidden;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
